import React, {useEffect,useState} from 'react'
import './users.css'

function User(){

  const [user, setUser] = useState([])


    useEffect(()=>{

      fetch('http://127.0.0.1:8000/api/users')
            .then((response) => {
              return response.json();
            })
            .then((data) => {
              console.log(data)
              setUser(data)
            })
            .catch(error=>console.log(error))

        },[])

  return(
      <div className="users-list">
        {
          user.map(x=>{
            return(
               <div className="user-item" key={x.id}>
                <h3>ФИО: {x.name}</h3>
                <p>Телефон: {x.phone}</p>
                <p>Адресс: {x.adress}</p>
                <p>ИНН: {x.id_num}</p>
              </div>
            )
           
          })
        }
        
      </div>
    )
}
export default User